"use client";

import { useState } from "react";
import { GalaxyMap } from "@/components/game/GalaxyMap";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Progress } from "@/components/ui/progress";

interface PlanetDetails {
  id: string;
  name: string;
  type: "normal" | "resource" | "station";
  resources: {
    metal: number;
    crystal: number;
    energy: number;
  };
  defenseLevel: number;
}

const mockPlanetDetails: Record<string, PlanetDetails> = {
  "planet-1": {
    id: "planet-1",
    name: "Alpha Centauri B2",
    type: "resource",
    resources: {
      metal: 75,
      crystal: 45,
      energy: 90
    },
    defenseLevel: 60
  },
  // Add more mock planets as needed
};

export default function ConquestPage() {
  const [selectedPlanet, setSelectedPlanet] = useState<PlanetDetails | null>(null);

  const handlePlanetSelect = (planetId: string) => {
    // In a real app, this would fetch planet details from an API
    setSelectedPlanet(mockPlanetDetails[planetId] || {
      id: planetId,
      name: `Unknown Planet ${planetId}`,
      type: "normal",
      resources: {
        metal: Math.random() * 100,
        crystal: Math.random() * 100,
        energy: Math.random() * 100
      },
      defenseLevel: Math.random() * 100
    });
  };

  return (
    <div className="min-h-screen bg-black text-white">
      {/* Header */}
      <div className="p-8">
        <h1 className="text-4xl font-bold mb-4 bg-gradient-to-r from-blue-500 to-purple-600 bg-clip-text text-transparent">
          Galactic Conquest
        </h1>
        <p className="text-gray-400">
          Explore and conquer planets across the vast galaxy
        </p>
      </div>

      {/* Main Content */}
      <div className="flex flex-col lg:flex-row gap-8 p-8 pt-0">
        {/* Galaxy Map */}
        <div className="flex-grow">
          <GalaxyMap onPlanetSelect={handlePlanetSelect} />
        </div>

        {/* Planet Details Sidebar */}
        <div className="lg:w-80">
          {selectedPlanet ? (
            <Card className="p-6 bg-gray-900 border-gray-800">
              <h2 className="text-2xl font-bold mb-4 text-blue-400">
                {selectedPlanet.name}
              </h2>
              
              <div className="space-y-6">
                {/* Planet Type */}
                <div>
                  <h3 className="text-sm text-gray-400 mb-2">Planet Type</h3>
                  <span className="inline-block px-3 py-1 rounded-full bg-blue-900 text-blue-300">
                    {selectedPlanet.type.charAt(0).toUpperCase() + selectedPlanet.type.slice(1)}
                  </span>
                </div>

                {/* Resources */}
                <div>
                  <h3 className="text-sm text-gray-400 mb-2">Resources</h3>
                  <div className="space-y-3">
                    <ResourceBar 
                      label="Metal" 
                      value={selectedPlanet.resources.metal} 
                      color="bg-gray-400" 
                    />
                    <ResourceBar 
                      label="Crystal" 
                      value={selectedPlanet.resources.crystal} 
                      color="bg-blue-400" 
                    />
                    <ResourceBar 
                      label="Energy" 
                      value={selectedPlanet.resources.energy} 
                      color="bg-yellow-400" 
                    />
                  </div>
                </div>

                {/* Defense Level */}
                <div>
                  <h3 className="text-sm text-gray-400 mb-2">Defense Level</h3>
                  <div className="space-y-2">
                    <div className="flex justify-between text-sm">
                      <span>Strength</span>
                      <span>{selectedPlanet.defenseLevel}%</span>
                    </div>
                    <Progress value={selectedPlanet.defenseLevel} className="h-2" />
                  </div>
                </div>

                {/* Actions */}
                <div className="space-y-2">
                  <Button className="w-full bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700">
                    Attack Planet
                  </Button>
                  <Button variant="outline" className="w-full border-blue-500 text-blue-500 hover:bg-blue-950">
                    Establish Trade Route
                  </Button>
                </div>
              </div>
            </Card>
          ) : (
            <Card className="p-6 bg-gray-900 border-gray-800">
              <p className="text-gray-400 text-center">
                Select a planet to view details
              </p>
            </Card>
          )}
        </div>
      </div>
    </div>
  );
}

function ResourceBar({ label, value, color }: { label: string; value: number; color: string }) {
  return (
    <div>
      <div className="flex justify-between text-sm mb-1">
        <span>{label}</span>
        <span>{value}%</span>
      </div>
      <div className="h-2 bg-gray-800 rounded-full overflow-hidden">
        <div 
          className={`h-full ${color} transition-all`}
          style={{ width: `${value}%` }}
        />
      </div>
    </div>
  );
}
