"use client";

import { useState, useEffect, useRef } from "react";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface Planet {
  id: string;
  name: string;
  x: number;
  y: number;
  size: number;
  type: "normal" | "resource" | "station";
  owned: boolean;
}

interface GalaxyMapProps {
  onPlanetSelect?: (planetId: string) => void;
}

export function GalaxyMap({ onPlanetSelect }: GalaxyMapProps) {
  const [planets, setPlanets] = useState<Planet[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [scale, setScale] = useState(1);
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const [isDragging, setIsDragging] = useState(false);
  const [startPos, setStartPos] = useState({ x: 0, y: 0 });
  const mapRef = useRef<HTMLDivElement>(null);

  // Generate random planets
  useEffect(() => {
    try {
      const generatedPlanets: Planet[] = Array.from({ length: 20 }, (_, i) => ({
        id: `planet-${i}`,
        name: `Planet ${i + 1}`,
        x: Math.random() * 2000 - 1000,
        y: Math.random() * 2000 - 1000,
        size: Math.random() * 20 + 10,
        type: Math.random() > 0.7 
          ? "resource" 
          : Math.random() > 0.8 ? "station" : "normal",
        owned: Math.random() > 0.8
      }));
      setPlanets(generatedPlanets);
    } catch (err) {
      setError("Failed to generate galaxy map");
    }
  }, []);

  const handleMouseDown = (e: React.MouseEvent) => {
    setIsDragging(true);
    setStartPos({
      x: e.clientX - position.x,
      y: e.clientY - position.y
    });
  };

  const handleMouseMove = (e: React.MouseEvent) => {
    if (!isDragging) return;
    
    setPosition({
      x: e.clientX - startPos.x,
      y: e.clientY - startPos.y
    });
  };

  const handleMouseUp = () => {
    setIsDragging(false);
  };

  const handleWheel = (e: React.WheelEvent) => {
    e.preventDefault();
    const newScale = scale - e.deltaY * 0.001;
    setScale(Math.min(Math.max(0.5, newScale), 2));
  };

  const getPlanetColor = (type: Planet["type"], owned: boolean) => {
    if (owned) return "bg-blue-500";
    switch (type) {
      case "resource": return "bg-green-500";
      case "station": return "bg-purple-500";
      default: return "bg-gray-500";
    }
  };

  return (
    <div className="relative w-full h-[calc(100vh-8rem)] overflow-hidden bg-black rounded-lg">
      {error && (
        <Alert variant="destructive" className="absolute top-4 left-4 right-4 z-50">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {/* Controls */}
      <div className="absolute top-4 right-4 z-40 space-y-2">
        <Button
          onClick={() => setScale(scale + 0.1)}
          className="w-10 h-10 rounded-full bg-gray-800/80 hover:bg-gray-700/80"
        >
          +
        </Button>
        <Button
          onClick={() => setScale(scale - 0.1)}
          className="w-10 h-10 rounded-full bg-gray-800/80 hover:bg-gray-700/80"
        >
          -
        </Button>
      </div>

      {/* Legend */}
      <Card className="absolute bottom-4 left-4 z-40 p-4 bg-gray-900/80 border-gray-800">
        <div className="space-y-2">
          <div className="flex items-center gap-2">
            <div className="w-3 h-3 rounded-full bg-gray-500" />
            <span className="text-sm text-gray-300">Normal Planet</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-3 h-3 rounded-full bg-green-500" />
            <span className="text-sm text-gray-300">Resource Planet</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-3 h-3 rounded-full bg-purple-500" />
            <span className="text-sm text-gray-300">Space Station</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-3 h-3 rounded-full bg-blue-500" />
            <span className="text-sm text-gray-300">Owned</span>
          </div>
        </div>
      </Card>

      {/* Galaxy Map */}
      <div
        ref={mapRef}
        className="absolute w-full h-full cursor-move"
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
        onWheel={handleWheel}
        style={{
          transform: `translate(${position.x}px, ${position.y}px) scale(${scale})`,
          transition: isDragging ? "none" : "transform 0.1s ease-out"
        }}
      >
        {/* Stars background */}
        <div className="absolute inset-0 bg-[radial-gradient(white_1px,transparent_1px)] bg-[length:50px_50px]" />

        {/* Planets */}
        {planets.map((planet) => (
          <div
            key={planet.id}
            className={`absolute rounded-full cursor-pointer transition-transform hover:scale-110
                       ${getPlanetColor(planet.type, planet.owned)}`}
            style={{
              width: `${planet.size}px`,
              height: `${planet.size}px`,
              left: `${planet.x + 1000}px`,
              top: `${planet.y + 1000}px`,
              transform: `translate(-50%, -50%)`,
              boxShadow: `0 0 ${planet.size/2}px ${getPlanetColor(planet.type, planet.owned).replace('bg-', 'rgb-').replace('500', '400')}`
            }}
            onClick={() => onPlanetSelect?.(planet.id)}
            title={planet.name}
          />
        ))}
      </div>
    </div>
  );
}
